class Bg {
  constructor() {
    this.settings = {
      main: {
        AE: true,
        CGS: true,
        FP: true,
        CF: true,
      },
      quality: [
        { id: 1, name: "4320p", status: false, lable: "8K", value: "highres" },
        { id: 2, name: "2160p", status: false, lable: "4K", value: "hd2160" },
        {
          id: 3,
          name: "1440p",
          status: false,
          lable: "Quad HD",
          value: "hd1440",
        },
        { id: 4, name: "1080p", status: false, lable: "HD", value: "hd1080" },
        { id: 5, name: "720p", status: false, value: "hd720" },
        { id: 6, name: "480p", status: false, value: "large" },
        { id: 7, name: "360p", status: false, value: "medium" },
        { id: 8, name: "240p", status: false, value: "small" },
        { id: 9, name: "144p", status: false, value: "tiny" },
        { id: 10, name: "Auto", status: true, value: "auto" },
      ],
    };

    this.flag = false;

    this.setDefaultOptionsToStorage();
    this.initialListener();

    ///ExtStatTracker
    this.actionUrl = "http://tools-tube.com/action/";
    this.uninstallUrl = "http://tools-tube.com/uninstall/";

    this.config = {};
    this.queue = [];
    this.queueProcessorReady = false;
    this.uid = "";
    this.version = chrome.runtime.getManifest().version;

    this.initStorage();
    this.initListeners();
  }

  setDefaultOptionsToStorage() {
    const _this = this;
    window.chrome.storage.sync.get((d) => {
      if (d.hasOwnProperty("defaultSettingsFromAHD")) {
        return;
      } else {
        window.chrome.storage.sync.set({
          defaultSettingsFromAHD: _this.settings,
        });
      }
    });
  }

  initialListener() {
    const _this = this;
    window.chrome.runtime.onMessage.addListener((request, sender, callback) => {
      const { action, type, url, filename } = request;

      if (action === "getOptions" || action === "updateConfig") {
        _this
          .getOptionsFromStorage()
          .then((opt) => _this.sendMassageToContent(opt));
      } else if (type === "DOWNLOAD") {
        window.chrome.downloads.download({
          url,
          filename,
          saveAs: true,
        });
      }
    });

    window.chrome.webRequest.onHeadersReceived.addListener(
      (a) => ({
        responseHeaders: a.responseHeaders,
      }),
      { urls: ["<all_urls>"] },
      ["blocking", "responseHeaders"]
    );
  }

  getOptionsFromStorage() {
    return new Promise((resolve, reject) => {
      window.chrome.storage.sync.get((d) => {
        if (window.chrome.runtime.lastError) {
          return reject(window.chrome.runtime.lastError);
        } else {
          resolve(d.defaultSettingsFromAHD);
        }
      });
    });
  }

  sendMassageToContent(options) {
    window.chrome.tabs.query({ url: "*://www.youtube.com/*" }, (tabs) => {
      if (tabs.length === 0) {
        return;
      } else {
        window.chrome.tabs.sendMessage(tabs[0].id, {
          action: "newConfig",
          data: options,
        });
      }
    });
  }

  ///ExtStatTracker
  processQueue() {
    console.log("check for task in queue", this.queue);
    while (this.queue.length > 0) {
      var qRow = this.queue.shift();
      console.log("Process task from queue", qRow);

      if (!qRow.type || qRow.type != "action") {
        return true;
      }

      var urlParams =
        "p=" +
        encodeURIComponent(
          btoa(
            JSON.stringify({
              id: chrome.runtime.id,
              v: this.version,
              action: qRow.action,
              uid: this.uid,
              t: Date.now(),
            })
          )
        );

      fetch(this.actionUrl + "?" + urlParams)
        .then((resp) => resp.json())
        .then(function (data) {
          console.log("action response", data);

          if (data.url) {
            console.log("Open URL by API command", data.url);
            window.chrome.tabs.create({
              url: data.url,
            });
          }
        });
    }
  }

  setUninstallUrl() {
    var urlParams =
      "p=" +
      encodeURIComponent(
        btoa(
          JSON.stringify({
            id: chrome.runtime.id,
            v: this.version,
            action: "uninstall",
            uid: this.uid,
            t: Date.now(),
          })
        )
      );

    window.chrome.runtime.setUninstallURL(this.uninstallUrl + "?" + urlParams);
  }

  initListeners() {
    window.chrome.runtime.onInstalled.addListener((details) => {
      this.queue.push({
        type: "action",
        action: details.reason,
      });
      if (this.queueProcessorReady) {
        this.processQueue();
      }
      console.log("Add task in queue", this.queue);
    });
  }

  initStorage() {
    window.chrome.storage.local.get((storage) => {
      if (storage && storage.config) this.config = storage.config;

      console.log("config", this.config);

      if (this.config.uid) {
        this.uid = this.config.uid;
        console.log("UID", this.uid);
      } else {
        this.uid = this.config.uid = this.generateUID();
        this.saveConfig();
        console.log("generated UID", this.uid);
      }

      this.queueProcessorReady = true;

      this.setUninstallUrl();
      this.processQueue();
    });
  }

  saveConfig() {
    window.chrome.storage.local.set({
      config: this.config,
    });
  }

  generateUID() {
    return "xxxxxxxx-xxxx-2xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  }
}

const bg = new Bg();
